import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { withStyles } from "@material-ui/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import PropTypes from "prop-types";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import CompBook from "../../public/icons/refferalApply.svg";
import RoboFreaks from "../../public/robofreaks-logo.webp";
import Hidden from "@material-ui/core/Hidden";
import { CircularProgress } from "@material-ui/core";
import "./ApplyRefferal.css";

const useStyles = (theme) => ({
  root: {
    minWidth: 275,
    borderRadius: "10px",
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
});

class ApplyRefferal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      fname: "",
      lname: "",
      pass1: "",
      pass2: "",
      username: "",
      studentgrade: 2,
      touched: {
        fname: false,
        lname: false,
        pass1: false,
        pass2: false,
        email: false,
        username: false,
        number: false,
      },
      hidden1: true,
      hidden2: true,
      submitted: false,
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
    this.toggleShow1 = this.toggleShow1.bind(this);
    this.toggleShow2 = this.toggleShow2.bind(this);
    this.onSuccessSignup = this.onSuccessSignup.bind(this);
    this.onFailedSignUp = this.onFailedSignUp.bind(this);
  }
  handleBlur = (field) => (evt) => {
    this.setState({
      touched: { ...this.state.touched, [field]: true },
    });
  };
  handleInputChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    this.setState({
      [name]: value,
    });
  }
  onSuccessSignup() {
    this.setState({ submitted: false });
    this.props.history.push("/data");
  }
  onFailedSignUp() {
    this.setState({ submitted: false });
  }
  handleSubmit(event) {
    this.setState({ submitted: true });
    let code = this.props.history.location.pathname.split("/")[2];
    this.props.authSignup(
      this.state.username,
      this.state.email,
      this.state.pass1,
      this.state.fname,
      this.state.lname,
      code,
      this.onSuccessSignup,
      this.onFailedSignUp
    );
    // alert("Current State is: " + JSON.stringify(this.state));
    // this.setState({
    //   email: "",
    //   question: "",
    //   fname: "",
    //   lname: "",
    //   studentgrade: 2,
    // });
    // window.open("/", "_self");
    event.preventDefault();
  }
  validate(fname, lname, pass1, email, pass2, username) {
    const errors = {
      fname: "",
      lname: "",
      pass1: "",
      email: "",
      pass2: "",
      username: "",
    };

    if (this.state.touched.fname && fname.length < 3) {
      errors.fname = "First Name should be >= 3 characters";
    } else if (this.state.touched.fname && fname.length > 10)
      errors.fname = "First Name should be <= 15 characters";

    if (this.state.touched.lname && lname.length < 3)
      errors.lname = "Last Name should be >= 3 characters";
    else if (this.state.touched.lname && lname.length > 10)
      errors.lname = "Last Name should be <= 15 characters";

    // if (this.state.touched.parentName && parentName.length < 3)
    //   errors.parentName = "Parent Name should be >= 3 characters";
    // else if (this.state.touched.parentName && parentName.length > 10)
    //   errors.parentName = "Parent Name should be <= 10 characters";

    if (this.state.touched.email) {
      if (
        !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
          email
        )
      )
        errors.email = "Invalid Email Address";
    }

    if (this.state.touched.username) {
      if (!/^[a-zA-Z0-9_]+$/i.test(username))
        errors.username = "Invalid Username";
    }

    if (this.state.touched.pass1) {
      if (
        !/^(?=.*[a-z])(?=.*\d)(?=.*[$@$!%*#?&<>^()])[a-z\d$@$!%*#?&<>^()]{8,}$/i.test(
          pass1
        )
      )
        errors.pass1 = "Invalid password";
    }

    if (this.state.touched.pass2) {
      if (this.state.pass1 !== pass2) errors.pass2 = "password does not match";
    }

    return errors;
  }

  toggleShow1() {
    this.setState({ hidden1: !this.state.hidden1 });
  }

  toggleShow2() {
    this.setState({ hidden2: !this.state.hidden2 });
  }

  render() {
    const { classes } = this.props;
    const errors = this.validate(
      this.state.fname,
      this.state.lname,
      this.state.pass1,
      this.state.email,
      this.state.pass2,
      this.state.username
    );
    return (
      <>
        <div className="container-fluid mt-4">
          <Grid container justify="space-around">
            <Grid item xs={12} style={{ zIndex: "1", paddingBottom: "0" }}>
              <div
                style={{
                  color: "#fff",
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <img src={RoboFreaks} alt="robofreaks" height={40} width={40} />
                <a href="/home" style={{ textDecoration: "none" }}>
                  <Typography
                    variant="h5"
                    component="h2"
                    style={{
                      lineHeight: "41px",
                      cursor: "pointer",
                      color: "#fff",
                    }}
                  >
                    RoboFreaks
                  </Typography>
                </a>
              </div>
            </Grid>
            <Hidden smDown>
              <Grid item style={{ zIndex: "1", paddingTop: "0" }}>
                <img
                  alt="CompBook"
                  src={CompBook}
                  height={500}
                  width={500}
                  style={{ filter: "drop-shadow(5px 5px 5px #404040)" }}
                />
              </Grid>
            </Hidden>
            <Grid item style={{ zIndex: "1", paddingTop: "2.5em" }}>
              <Typography
                variant="h6"
                gutterBottom
                style={{ color: "#fff", textAlign: "center" }}
              >
                Welcome to ROBOFREAKS
              </Typography>
              <Card className={classes.root} variant="outlined" elevation={5}>
                <CardContent>
                  {/* <Typography
                    paragraph
                    gutterBottom
                    style={{ textAlign: "center", marginBottom: "1.5em" }}
                  >
                    Some text
                  </Typography> */}
                  <form onSubmit={this.handleSubmit}>
                    <div className="row mb-3">
                      <div className="form-group col-6">
                        <label htmlFor="fname">First Name</label>
                        <input
                          type="text"
                          className="form-control"
                          id="fname"
                          name="fname"
                          placeholder="Student's First Name"
                          value={this.state.fname}
                          valid={errors.fname === ""}
                          invalid={errors.fname !== ""}
                          onBlur={this.handleBlur("fname")}
                          onChange={this.handleInputChange}
                          style={{ height: 50 }}
                          required
                        />
                        <small className="text-danger">{errors.fname}</small>
                      </div>
                      <div className="form-group col-6">
                        <label htmlFor="lname">Last Name</label>
                        <input
                          type="text"
                          className="form-control"
                          id="lname"
                          name="lname"
                          placeholder="Student's Last Name"
                          value={this.state.lname}
                          valid={errors.lname === ""}
                          invalid={errors.lname !== ""}
                          onBlur={this.handleBlur("lname")}
                          onChange={this.handleInputChange}
                          style={{ height: 50 }}
                          required
                        />
                        <small className="text-danger">{errors.lname}</small>
                      </div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="username">Username</label>
                      <input
                        type="text"
                        className="form-control"
                        id="username"
                        name="username"
                        placeholder="Enter username"
                        value={this.state.username}
                        valid={errors.username === ""}
                        invalid={errors.username !== ""}
                        onBlur={this.handleBlur("username")}
                        onChange={this.handleInputChange}
                        style={{ height: 50 }}
                        required
                      />
                      <small className="text-danger">{errors.username}</small>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleInputEmail1">Email address</label>
                      <input
                        type="email"
                        className="form-control"
                        id="exampleInputEmail1"
                        name="email"
                        placeholder="Enter email"
                        value={this.state.email}
                        valid={errors.email === ""}
                        invalid={errors.email !== ""}
                        onBlur={this.handleBlur("email")}
                        onChange={this.handleInputChange}
                        style={{ height: 50 }}
                        required
                      />
                      <small className="text-danger">{errors.email}</small>
                    </div>
                    {/* <div className="form-group">
                      <label htmlFor="number">Contact Number</label>
                      <input
                        type="number"
                        className="form-control"
                        id="number"
                        name="number"
                        placeholder="Enter Contact Number"
                        value={this.state.number}
                        valid={errors.number === ""}
                        invalid={errors.number !== ""}
                        onBlur={this.handleBlur("number")}
                        onChange={this.handleInputChange}
                        style={{ height: 50 }}
                        required
                      />
                      <small className="text-danger">{errors.number}</small>
                    </div> */}
                    <div className="form-group">
                      <label htmlFor="pass1">Password</label>
                      <div className="border password d-flex">
                        <input
                          type={this.state.hidden1 ? "password" : "text"}
                          className="form-control"
                          id="pass1"
                          name="pass1"
                          placeholder="Password"
                          value={this.state.pass1}
                          valid={errors.pass1 === ""}
                          invalid={errors.pass1 !== ""}
                          onBlur={this.handleBlur("pass1")}
                          onChange={this.handleInputChange}
                          required
                          style={{
                            border: "none",
                            outline: "none",
                            height: 50,
                          }}
                        />
                        <span
                          role="button"
                          tabIndex={0}
                          onClick={this.toggleShow1}
                          onKeyDown={this.toggleShow1}
                          style={{ outline: "none", fontSize: 20 }}
                          md={2}
                          className={
                            this.state.hidden1
                              ? "fa fa-eye pt-3 pr-2"
                              : "fa fa-eye-slash pt-3 pr-2"
                          }
                          id="togglePassword"
                        ></span>
                      </div>
                      <small className="text-danger">{errors.pass1}</small>
                    </div>
                    <div className="form-group">
                      <label htmlFor="pass2">Confirm Password</label>
                      <div className="border password d-flex">
                        <input
                          type={this.state.hidden2 ? "password" : "text"}
                          className="form-control"
                          id="pass2"
                          name="pass2"
                          placeholder="Confirm Password"
                          value={this.state.pass2}
                          valid={errors.pass2 === ""}
                          invalid={errors.pass2 !== ""}
                          onBlur={this.handleBlur("pass2")}
                          onChange={this.handleInputChange}
                          required
                          style={{
                            border: "none",
                            outline: "none",
                            height: 50,
                          }}
                        />
                        <span
                          role="button"
                          tabIndex={0}
                          onClick={this.toggleShow2}
                          onKeyDown={this.toggleShow2}
                          style={{ outline: "none", fontSize: 20 }}
                          md={2}
                          className={
                            this.state.hidden2
                              ? "fa fa-eye pt-3 pr-2"
                              : "fa fa-eye-slash pt-3 pr-2"
                          }
                          id="togglePassword"
                        ></span>
                      </div>
                      <small className="text-danger">{errors.pass2}</small>
                    </div>
                    <div className="row form-group mt-5">
                      <div className="col col-12">
                        <Button
                          variant="contained"
                          type="submit"
                          fullWidth={true}
                          style={{
                            backgroundColor: "#09bfad",
                            color: "white",
                            outline: "none",
                            height: 50,
                          }}
                        >
                          {this.state.submitted ? (
                            <CircularProgress style={{ color: "white" }} />
                          ) : (
                            "Submit"
                          )}
                        </Button>
                      </div>
                    </div>
                  </form>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </div>
        <div className="refferaldiv">
          <div className="child">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
              <path
                fill="#fff"
                fill-opacity="1"
                d="M0,256L40,261.3C80,267,160,277,240,272C320,267,400,245,480,197.3C560,149,640,75,720,58.7C800,43,880,85,960,101.3C1040,117,1120,107,1200,85.3C1280,64,1360,32,1400,16L1440,0L1440,320L1400,320C1360,320,1280,320,1200,320C1120,320,1040,320,960,320C880,320,800,320,720,320C640,320,560,320,480,320C400,320,320,320,240,320C160,320,80,320,40,320L0,320Z"
              ></path>
            </svg>
          </div>
        </div>
        <br />
        <br />
      </>
    );
  }
}
ApplyRefferal.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(useStyles)(withRouter(ApplyRefferal));
